* {
    /* change the default since I haven't gotten to accounting borders yet*/
    box-sizing: border-box;
}
#container {
    border: 3px solid #6495ED;
    display: inline-flex;
    flex-direction: column;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    border-radius: 12px;
    padding: 15px 15px;
}

#output, .boxes {
    margin: 0 0.5em;
}

.boxes {
    float: left;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.lockSize {
    position: relative;
    width: 100%;
}
.lockSize > * {
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
}

#diff {
    overflow: auto;
    text-align: left;
    word-wrap: break-word;
    white-space: pre-wrap;
}

#output {
    display: flex;
    flex-direction: column;
    resize: block;
}

#container > div {
    display: flex;
    flex-direction: row;
}

#versions {
    flex-grow: 3;
}

#diff-container {
    flex: 1 2;
}

.input {
    flex-grow: 1;
    flex-shrink: 0;
}

#output {
    /*flex-basis: 50%;*/
    max-width: 50%;
    flex-grow: 2;
    flex-shrink: 1;
}

.boxes > h2 {
    text-align: center;
}

del, ins, .com {
    font-weight: bold
}


textarea {
    flex-grow: 1;
    /* resizing doesn't really do anything, and it causes the boxes to pop out for some reason.*/
    resize: none;
}

#panel, #export {
    flex-grow: 1;
    /*flex-basis: %;*/
    justify-content: space-evenly;
}

#panel {
    align-self: stretch;
    /*grid-row: 2;*/
    color: #483D8B;
    text-align: center;
    font-weight: bold;
    margin-left: 5px;
    display: flex;
    flex-direction: column;
}

#panel > div {
    display: flex;
    flex-direction: row;
    /*align-content: flex-start;*/
    justify-content: center;
}
#panel label {
    align-self: center;
}

#export > * {
    flex: 1 1;
}

button {
    align-self: center;
    text-align: center;
}

#export > textarea {
    flex-shrink: 0;
}

/* Left over from react creation, but still using */
.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    /*font-size: calc(10px + 2vmin);*/
    color: white;
}

.App-link {
    color: #61dafb;
}
